import React, { useState } from "react";
import classNames from "classnames/bind";
import styles from "./Donate.module.scss";
import "./Donate.css";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import { ToastContainer, toast } from "react-toastify";

const cx = classNames.bind(styles);
export default function Donate() {
  const [money, setMoney] = useState("$ | 25");
  const donate = () => {
    if (
      money === "$ | " ||
      money === "$ |" ||
      money === "" ||
      money === " "
    ) {
      toast.warning("Please choose or type a number.");
    } else {
      toast.success("Donate sucessfull. We appreciate your contributions.");
    }
  };
  const handleChange = (e) => {
    setMoney(e.target.value);
  };
  const handle10 = () => {
    setMoney("$ | 10");
  };
  const handle25 = () => {
    setMoney("$ | 25");
  };
  const handle50 = () => {
    setMoney("$ | 50");
  };
  const handle100 = () => {
    setMoney("$ | 100");
  };
  const handle250 = () => {
    setMoney("$ | 250");
  };
  const custom = () => {
    setMoney("$ | ");
  };
  // console.log(setMoney);
  return (
    <div>
      <div className={cx("wrapper")}>
        <div className={cx("background")}>
          <img
            src="https://hopedonors.org/wp-content/uploads/2022/02/Donate-now.jpg"
            alt=""
          ></img>
        </div>
        <div className={cx("card-donate")}>
          <div className={cx("title-donate")}>
            <h2>Your Gift to The Animals Saves Lives</h2>
            <p>
              Help our organization by donating today! All donations go directly
              to making a difference for our cause.
            </p>
          </div>
          <div className={cx("input-donate")}>
            <input onChange={handleChange} value={money}></input>
          </div>
          <div className={cx("money-donate")}>
            <div className={cx("donate-10")}>
              <button onClick={handle10}>10</button>
            </div>
            <div className={cx("donate-25")}>
              <button onClick={handle25}>25</button>
            </div>
            <div className={cx("donate-50")}>
              <button onClick={handle50}>50</button>
            </div>
            <div className={cx("donate-100")}>
              <button onClick={handle100}>100</button>
            </div>
            <div className={cx("donate-250")}>
              <button onClick={handle250}>250</button>
            </div>
            <div className={cx("donate-custom")}>
              <button onClick={custom}>Custom Donate</button>
            </div>
          </div>
          <button className={cx("button")} onClick={donate}>
            <div style={{ display: "flex", alignItems: "center" }}>
              Donate Now <ArrowForwardIosIcon></ArrowForwardIosIcon>
            </div>
          </button>
          <ToastContainer
            autoClose={2000}
            pauseOnHover={false}
          ></ToastContainer>
        </div>
      </div>
    </div>
  );
}
