import React, { useContext } from "react";
import classNames from "classnames/bind";
import styles from "./News2.module.scss";
import { CartContext } from "../../Context/CartContext";
import { Link } from "react-router-dom";

const cx = classNames.bind(styles);

export default function News2(props) {
  const { setDetailProject } = useContext(CartContext);
  const { img, title, summary, challenge, solution, impact } = props;
  const handleClick = () => {
    const newData = {
      addImgAnimal: img,
      addTitleAnimal: title,
      addSummaryAnimal: summary,
      addChallengeAnimal: challenge,
      addSolutionAnimal: solution,
      addImpactAnimal: impact,
    };
    setDetailProject([newData]);
  };
  return (
    <div>
      <div className={cx("wrapper")}>
        <div className={cx("card")}>
          <div className={cx("img")}>
            <img src={img} alt="" />
            <div className={cx("text")}>
              <div className={cx("title")}>
                <h1>{title}</h1>
                <span></span>
              </div>
              <div className={cx("desc")}>
                <p>{summary}</p>
                <p>{challenge}</p>
              </div>
              <Link onClick={handleClick} to="/detail-project">
                <div className={cx("read-more")}>
                  <p>Read More</p>
                </div>
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
