import React, { useContext, useState } from "react";
import classNames from "classnames/bind";
import styles from "./DogCard.module.scss";
import { CartContext } from "../../Context/CartContext";
import { Link } from "react-router-dom";
import FavoriteIcon from "@mui/icons-material/Favorite";
import FavoriteBorderIcon from "@mui/icons-material/FavoriteBorder";
const cx = classNames.bind(styles);
export default function DogCard(props) {
  //nhn props t DogsPage
  const {
    idDog,
    breedDog,
    descDog,
    imgDog,
    heightDog,
    weightDog,
    lifeDog,
    breedGroupDog,
  } = props;
  //---------------------------------------------------------------

  //S dng nh vy  thay i cc useState(v sau), cn (v u s dng cho cc trang khc)
  const { addToCart, setDetail } = useContext(CartContext);
  //---------------------------------------------------------------
  //useState sau  thay i ch addToCart -> you already add
  const [isAdded, setIsAdded] = useState(false);
  //---------------------------------------------------------------

  //handeClick khi nhn vo button s to ra mt danh sch NewItems={...thng tin cn truyn ra ngoi, ( consolelog  r )}
  const handleClick = () => {
    setIsAdded((isAdded) => !isAdded);
    const newItems = {
      addImg: imgDog,
      addBreedDog: breedDog,
      addBreedGroupDog: breedGroupDog,
      addIdDog: idDog,
      addDescDog: descDog,
      addHeightDog: heightDog,
      addWeightDog: weightDog,
      addLifeDog: lifeDog,
    };

    // setLove((love)=>[...love])
    //S dng destructuring  hin thi thm cc object khc
    addToCart((item) => [...item, newItems]);
    //Set total nh sau  cng dn s ( total += Number )
  };
  //---------------------------------------------------------------

  //detailClick khi nhn vo card s truyn ra mt danh sch newItem2 ={thng tin ch cn truyn ra ngoi}
  const detailClick = () => {
    const newItems2 = {
      addIdDog: idDog,
      addBreedDog: breedDog,
      addImg: imgDog,
      addDescDog: descDog,
      addHeightDog: heightDog,
      addWeightDog: weightDog,
      addLifeDog: lifeDog,
      addBreedGroupDog: breedGroupDog,
    };
    //Thm ch cha ( ch ch l setDetail ) bng vi danh sch va to. Nh l bin thm [ ]  th hin mt mng
    setDetail([newItems2]);
  };
  return (
    <div className={cx("card")}>
      {/* Dng Link  di chuyn ti path bn App.js */}
      <Link onClick={detailClick} to="/dogdetail">
        <div className={cx("top_card")}>
          <div className={cx("image_card")}>
            <img
              style={{ objectFit: "fill" }}
              className={cx("img")}
              src={imgDog}
              alt=""
            ></img>
          </div>
          <div style={{ width: "300px" }} className={cx("right_top_card")}>
            <h2 style={{ textTransform: "capitalize" }}>{breedDog}</h2>
          </div>
        </div>
        <div style={{ marginBottom: "20px" }} className={cx("mid_card")}>
          <h4>{descDog}</h4>
        </div>
      </Link>
      {/* Nu isAdded = true th hin you already add it, = false ( mc nh ban u ca useState ) th hin Add to cart */}
      {isAdded ? (
        <button className={cx("add-cart")} >
          <FavoriteIcon></FavoriteIcon>
        </button>
      ) : (
        <button  className={cx("add-cart")} onClick={handleClick}>
          <FavoriteBorderIcon></FavoriteBorderIcon>
        </button>
      )}
    </div>
  );
}
