import React from "react";
import classNames from "classnames/bind";
import styles from "./Impact.module.scss";
const cx = classNames.bind(styles);

export default function Impact() {
  return (
    <div>
      <div className={cx("wrapper")}>
        <div className={cx("banner")}>
          <img
            width="100vw"
            height="60vh"
            style={{ width: "100vw", height: "80vh" }}
            src="https://hopedonors.org/wp-content/uploads/2022/03/4525753.jpg"
            alt=""
          ></img>
        </div>
        <div className={cx("content")}>
          <h2>Our Impact</h2>
          <p>
            After the spread of Covid-19 which resulted in a worldwide pandemic
            in 2020, Hope Donors developed an action plan with a focus on 4
            global issues of poverty, animals, climate, and emergency crisis.
            The main goals of these plans are providing food and clean water to
            rural and poverty-ridden areas, take climate actions by providing
            energy using renewable resources and planting trees, protect animals
            by feeding and sheltering, and realizing our motto of first in, last
            out in emergencies. At Hope Donors, our priority is to have a strong
            presence in under-developed countries and have transparency in
            donation spending. To achieve our goals, in addition to our team, we
            have many volunteers in different countries.
          </p>
          <h2>Africa</h2>
          <p>
            We started our journey in Africa. In South Africa, in addition, to
            provide food to the homeless, we completed 10 clean water projects
            which resulted in access to clean water to 20,000 people. Also, to
            support stray animals, we performed developed and implemented a
            street animal rescue plan in Johannesburg, which resulted in saving
            more than 2000 animals. Mozambique is one of the poorest countries
            in Africa, and most of its populations live in rural areas with
            limited access to food and clean water. We were able to provide
            food, clean water, and childrens supplements to 10 villages. In
            Malawi, we provided educational aid and stationery for children and
            provided support to female farmers in 5 villages. In Zambia, where
            15% of the population has AIDS, we were able to provide food and
            medicine packages for people, help educational meetings and
            distribute condoms in rural areas to help prevent AIDS. In Tanzania,
            where almost 70 percent of children aged 1417 years are not
            enrolled in secondary education, we were able to provide educational
            aid and stationery for children in rural areas. In Rwanda, where
            about 39% of the population is below the poverty line, we provided
            educational aid and stationery for kids. In Kenya, we have completed
            projects to make 5 villages self-sufficient in food and access to
            clean water. In addition, we provided 7,000 families with basic
            medical needs. In Ethiopia, where 33% of the population lives in
            poverty, we provided food and supplements for malnourished children.
            In Nigeria, where access to clean water remains a challenge, we were
            able to give 50,000 people sustainable access to clean water and
            sanitation facilities by constructing water wells and water pipes.
            In addition, we sent 10,000 malaria test kits to help fight malaria.
            In Benin, where only 42% of the population are literate, we aim to
            empower schools and childrens education. Therefore, we have sent
            educational aid and stationery for students. In the Ivory Coast,
            where 59% of the population lives in poverty, our focus is to
            provide help for newborn babies. As a result, we have sent packages
            of food and medical supplement to childre
          </p>
          <h2>Asia</h2>
          <p>
            Afghanistan crisis is not limited to conflict, as it faces economic,
            humanitarian, and shortage of food crisis as well. Currently, many
            Afghans have fled their homes due to the Taliban take over and are
            living in a bad condition. We have considered the Afghanistan crisis
            an emergency and are actively sending food and water to the Afghan
            people. In addition, we are providing shelter, food, and clean water
            to Afghan refugees leaving their homes and country. We continue to
            provide food, clean water, and shelter to Afghan refugees until this
            crisis is over. India is facing the Covid-19 crisis and we are
            considering it an emergency crisis. Therefore, we are providing
            Covid-19 test kits, masks, medical and sanitary supplies for about
            60,000 people in 10 provinces. We continue to provide help in India
            until the Covid-19 crisis is over. In Sri Lanka, where about 25% of
            the population lives below the poverty line, we aim to provide
            educational support for children. As a result, we were able to
            provide 20,000 educational aid and stationery for children.{" "}
          </p>
        </div>
      </div>
    </div>
  );
}
