import React from "react";
import classNames from "classnames/bind";
import styles from "./Chart.module.scss";
import { CircularProgressbar } from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";
const cx = classNames.bind(styles);
export default function Chart() {
  const percentage = 45;
  const percentage1 = 26;
  const percentage2 = 23;
  const percentage3 = 6;

  return (
    <div>
      <div className={cx("wrapper")}>
        <div className={cx("title")}>
          <h1>Total Spending On Each Work</h1>
          <span></span>
        </div>
        <div className={cx("chart")}>
          <div className={cx("pie-1")} style={{ width: 180, height: 180 }}>
            <div className={cx("pie-info-1")}>
              <CircularProgressbar value={percentage} text={`${percentage}%`} />
              <p className={cx("title-pie")}>Animal Rescue</p>
            </div>
          </div>
          <div className={cx("pie-2")} style={{ width: 180, height: 180 }}>

            <div className={cx("pie-info-2")}>
              <CircularProgressbar
                value={percentage1}
                text={`${percentage1}%`}
              />
              <p className={cx("title-pie")}>Animal Rescue</p>
            </div>
          </div>
          <div className={cx("pie-3")} style={{ width: 180, height: 180 }}>

            <div className={cx("pie-info-3")}>
              <CircularProgressbar
                value={percentage2}
                text={`${percentage2}%`}
              />
              <p className={cx("title-pie")}>Animal Rescue</p>
            </div>
          </div>
          <div className={cx("pie-4")} style={{ width: 180, height: 180 }}>

            <div className={cx("pie-info-4")}>
              <CircularProgressbar
                value={percentage3}
                text={`${percentage3}%`}
              />
              <p className={cx("title-pie")}>Animal Rescue</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
