import React, { useContext } from "react";
import classNames from "classnames/bind";
import styles from "./Project.module.scss";
import DoubleArrowIcon from "@mui/icons-material/DoubleArrow";
import { CartContext } from "../../Context/CartContext";
import { Link } from "react-router-dom";
const cx = classNames.bind(styles);
export default function Project(props) {
  const { setDetailProject } = useContext(CartContext);
  const {
    imgProject,
    titleProject,
    summaryProject,
    challengeProject,
    solutionProject,
    impactProject,
    timeProject,
    descProject,
  } = props;
  const handleClick = () => {
     const newData = {
       addImgAnimal: imgProject,
       addTitleAnimal: titleProject,
       addSummaryAnimal: summaryProject,
       addChallengeAnimal: challengeProject,
       addSolutionAnimal: solutionProject,
       addImpactAnimal: impactProject,
     };
     setDetailProject([newData]);
  };
  return (
    <Link onClick={handleClick} to="/detail-project">
      <div className={cx("cards")}>
        <div className={cx("card")}>
          <div className={cx("card-pic")}>
            <img className={cx("img")} src={imgProject} alt=""></img>
          </div>
          <div className={cx("card-title")}>
            <h1>{titleProject}</h1>
            <p>{timeProject}</p>
          </div>
          <div className={cx("card-info")}>{descProject}</div>
          <div className={cx("read-more")} style={{ display: "flex" }}>
            Read More{" "}
            <DoubleArrowIcon
              style={{
                alignItems: "center",
                display: "flex",
                flexDirection: "row",
              }}
            ></DoubleArrowIcon>
          </div>
        </div>
      </div>
    </Link>
  );
}
