import React from "react";
import classNames from "classnames/bind";
import styles from "./Content.module.scss";
import './Content.css'
const cx = classNames.bind(styles);
export default function Content(props) {
  const { teamData } = props;
  return (
    <div>
      <div className={cx("wrapper")}>
        <div className={cx("top")} >
          <h2>Team</h2>
          <span></span>
          <p>
            Facing a problem as big as animal cruelty requires an incredible
            team. were grateful to have some of the most innovative and
            committed leaders guiding our organization. With your help, we try
            our best to mitigate the rate of animal cruelty all over the world.
          </p>
        </div>

        <div className={cx("cards")}>
          {teamData.map((data) => {
            return (
              <div className={cx("card")}>
                <img src={data.img} alt=""></img>
                <h2>{data.name}</h2>
                <p>{data.job}</p>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
}
