import React from "react";
import News2 from "./News2";

export default function NewsMain(props) {
  
  const { dataNews } = props;
  return (
    <div>
      {dataNews.map((data,idx) => {
        return (
          <News2
            key={idx}
            title={data.title}
            img={data.img}
            summary={data.summary}
            challenge={data.challenge}
            solution={data.solution}
            impact={data.impact}
          ></News2>
        );
      })}
    </div>
  );
}
