import "./App.css";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Home from "./components/Page/Home/Home";
import Navbar from "./components/Navbar/Navbar";
import { useEffect, useState } from "react";
import axios from "axios";
import { CartContext } from "./components/Context/CartContext";
import Footer from "./components/Footer/Footer";
import WhoWeAre from "./components/Page/Who-we-are/WhoWeAre";
import Team from "./components/Page/Team/Team";
import Partners from "./components/Page/Partners/Partners";
import AnimalRescue from "./components/Page/AnimalRescue/AnimalRescue";
import AnimalShelters from "./components/Page/AnimalShelters/AnimalShelters";
import Spay from "./components/Page/Spay/Spay";
import AnimalCare from "./components/Page/Care/AnimalCare";
import DetailProject from "./components/Page/IssuesWeWorkOn/DetailProject";
import Donate from "./components/Page/Donate/Donate";
import ContactUs from "./components/Page/ContactUs/ContactUs";
import Impact from "./components/Page/Impact/Impact";
import DogDetail from "./components/Page/DogDetail/DogDetail";
import DogsPage from "./components/Page/Dogs/DogsPage";
import CartPage from "./components/Page/Cart/CartPage";
import Main from "./components/Page/Cart/Main";
function App(props) {
  //Ly thng tin t backend bng axios v useEffect, mu nh sau:
  useEffect(() => {
    async function getData() {
      const res = await axios.get("/v1/dogs");
      return res;
    }
    async function getData2() {
      const resd = await axios.get("/projects");
      return resd;
    }
    async function getDataTeam() {
      const team = await axios.get("/team");
      return team;
    }
    async function getDataAnimalRescue() {
      const rescue = await axios.get("/rescue");
      return rescue;
    }
    async function getDataAnimalShelters() {
      const shelters = await axios.get("/shelters");
      return shelters;
    }
    async function getDataAnimalSpay() {
      const spay = await axios.get("/spay");
      return spay;
    }
    async function getDataAnimalCare() {
      const care = await axios.get("/care");
      return care;
    }
    async function getNews() {
      const news = await axios.get("/news");
      return news;
    }
    getData().then((res) => setAllDogs(res.data));
    getData2().then((resd) => setDataProject(resd.data));
    getDataTeam().then((team) => setTeamData(team.data));
    getDataAnimalRescue().then((rescue) => setAnimalRescueData(rescue.data));
    getDataAnimalShelters().then((shelters) =>
      setAnimalSheltersData(shelters.data)
    );
    getDataAnimalSpay().then((spay) => setAnimalSpayData(spay.data));
    getDataAnimalCare().then((care) => setAnimalCareData(care.data));
    getNews().then((news) => setNews(news.data));
  });

  //------------------------------------------

  const [detailProject, setDetailProject] = useState([{}]);
  const [news, setNews] = useState([]);
  const { love, setLove } = useState([]);

  const [animalSheltersData, setAnimalSheltersData] = useState([]);
  const [animalSpayData, setAnimalSpayData] = useState([]);
  const [animalCareData, setAnimalCareData] = useState([]);

  const [animalRescueData, setAnimalRescueData] = useState([]);
  const [teamData, setTeamData] = useState([]);
  const [dataProject, setDataProject] = useState([]);
  //------------------------------------------
  //Thm vo gi hng
  const [myCart, addToCart] = useState([{}]);
  //------------------------------------------

  //To ra chi tit con ch trong trang mi khi nhp vo
  const [detail, setDetail] = useState([{}]);
  //------------------------------------------

  //setAllDogs = API json, truyn props allDogs
  const [allDogs, setAllDogs] = useState([]);
  //------------------------------------------

  const [link, setLink] = useState("");
  const [text, setText] = useState("");
  return (
    //Bc bng Provider khi s dng React Context
    <CartContext.Provider
      //Bt buc set value khi s dng React Context
      value={{
        love,
        setLove,
        myCart,
        addToCart,
        detail,
        setDetail,
        link,
        setLink,
        detailProject,
        setDetailProject,
        text,
        setText,
      }}
    >
      {/* Router -> Routes -> Route, path l ng dn mun n*/}
      <Router>
        <Navbar></Navbar>
        <div className="page_container">
          <Routes>
            <Route
              path="/"
              element={<Home dataProject={dataProject} dataNews={news}></Home>}
            />
            <Route
              path="/dogs"
              //Truyn props API allDogs ra ngoi
              element={<DogsPage allDogs={allDogs}></DogsPage>}
            />
            <Route path="/cart" element={<Main></Main>} />
            <Route path="/dogdetail" element={<DogDetail></DogDetail>} />
            <Route path="/who-we-are" element={<WhoWeAre></WhoWeAre>} />
            <Route path="/team" element={<Team teamData={teamData}></Team>} />
            <Route path="/partners" element={<Partners></Partners>} />
            <Route
              path="/spay"
              element={<Spay animalSpayData={animalSpayData}></Spay>}
            />
            <Route
              path="/care"
              element={
                <AnimalCare animalCareData={animalCareData}></AnimalCare>
              }
            />
            <Route path="/our-impact" element={<Impact></Impact>} />

            <Route
              path="/animal-shelters"
              element={
                <AnimalShelters
                  animalSheltersData={animalSheltersData}
                ></AnimalShelters>
              }
            />

            <Route
              path="/animal-rescue"
              element={
                <AnimalRescue
                  animalRescueData={animalRescueData}
                ></AnimalRescue>
              }
            />
            <Route
              path="/detail-project"
              element={
                <DetailProject
                  animalRescueData={animalRescueData}
                ></DetailProject>
              }
            />
            <Route path="/donate" element={<Donate></Donate>} />
            <Route path="/contact-us" element={<ContactUs></ContactUs>} />
          </Routes>
        </div>
        <Footer></Footer>
      </Router>
    </CartContext.Provider>
  );
}

export default App;
