import React from "react";
import classNames from "classnames/bind";
import styles from "./News.module.scss";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import { Link } from "react-router-dom";
const cx = classNames.bind(styles);
export default function News() {
  return (
    <div>
      <div className={cx("wrapper")}>
        <div className={cx("card")}>
          <div className={cx("img")}>
            <img
              src="https://hopedonors.org/wp-content/uploads/2022/02/ph_10336_75500.jpg"
              alt=""
              width={100}
              height={100}
            ></img>
          </div>
          <div className={cx("right-card")}>
            <div className={cx("title-card")}>
              <h2>Your Impact In 2021</h2>
              <span></span>
            </div>
            <div className={cx("bottom-title")}>
              <h3>
                Hope Donors focus is on 4 global Animal issues: Animal Rescue,
                Animal Shelters, Veterinarian , and Spay And Neuter.
              </h3>
            </div>
            <div className={cx("info-card")}>
              <p style={{ paddingBottom: "20px" }}>
                At Hope Donors, our priority is to have a strong presence and
                have transparency in donation spending.
              </p>
              <p>
                To achieve our goals, in addition to our team, we have many
                volunteers in different countries.
              </p>
            </div>
           <Link to='/our-impact'>
              <div className={cx("read-more")} style={{ display: "flex",flexDirection:'row' }}>
                <button>
                  Read More{" "}
                  <ArrowForwardIosIcon
                    style={{ alignItems: "center", display: "flex" }}
                  ></ArrowForwardIosIcon>
                </button>
              </div>
           </Link>
          </div>
        </div>
      </div>
    </div>
  );
}
