import React from "react";
import classNames from "classnames/bind";
import styles from "./Donate.module.scss";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import { Link } from "react-router-dom";
const cx = classNames.bind(styles);

export default function Donate() {
  return (
    <div>
      <div className={cx("wrapper")}>
        <div className={cx("logo")}>
          <img
            src="https://hopedonors.org/wp-content/uploads/2022/02/hopedonors-logo-2.png"
            alt=""
          ></img>
        </div>
        <div className={cx("below-logo")}>
          <p>Save The Loving Pows</p>
        </div>
        <div className={cx("bottom")}>
          <h1>Help The Hope Donors Put A Stop To Animal Cruelty</h1>
          <Link to='/donate'>
            <button>
              Donate Now <ArrowForwardIosIcon></ArrowForwardIosIcon>
            </button>
          </Link>
        </div>
      </div>
    </div>
  );
}
