import React from "react";
import classNames from "classnames/bind";
import styles from "./Help.module.scss";
import PetsIcon from "@mui/icons-material/Pets";
import LocationCityIcon from "@mui/icons-material/LocationCity";
import MuseumIcon from "@mui/icons-material/Museum";
import RestaurantMenuIcon from "@mui/icons-material/RestaurantMenu";
const cx = classNames.bind(styles);
export default function Help() {
  return (
    <div>
      <div className={cx("wrapper")}>
        <div className={cx("title")}>
          <h1>Hope Donors Impact At A Glance</h1>
          <span></span>
        </div>
        <div className={cx("line")}>
          <div className={cx("card-1")}>
            <div className={cx("logo")}>
              <div className={cx("shadow-logo")}></div>
              <PetsIcon
                style={{ color: "#ff6300", position: "relative" }}
                classNames={cx("icon")}
              ></PetsIcon>
            </div>
            <div className={cx("amount")}>
              <p>90,000</p>
            </div>
            <div className={cx("name")}>
              <p>Animals Assisted</p>
            </div>
          </div>
          <div className={cx("card-1")}>
            <div className={cx("logo")}>
              <div className={cx("shadow-logo")}></div>
              <LocationCityIcon
                style={{ color: "#ff6300", position: "relative" }}
                classNames={cx("icon")}
              ></LocationCityIcon>
            </div>
            <div className={cx("amount")}>
              <p>9,000</p>
            </div>
            <div className={cx("name")}>
              <p>Animals Relocated</p>
            </div>
          </div>
          <div className={cx("card-1")}>
            <div className={cx("logo")}>
              <div className={cx("shadow-logo")}></div>
              <MuseumIcon
                style={{ color: "#ff6300", position: "relative" }}
                classNames={cx("icon")}
              ></MuseumIcon>
            </div>
            <div className={cx("amount")}>
              <p>500,000</p>
            </div>
            <div className={cx("name")}>
              <p>Animals Helped by Animal Poison Control Center</p>
            </div>
          </div>
          <div className={cx("card-1")}>
            <div className={cx("logo")}>
              <div className={cx("shadow-logo")}></div>
              <RestaurantMenuIcon
                style={{ color: "#ff6300", position: "relative" }}
                classNames={cx("icon")}
              ></RestaurantMenuIcon>
            </div>
            <div className={cx("amount")}>
              <p>2,500</p>
            </div>
            <div className={cx("name")}>
              <p>Spay/Neuter Surgeries Performed</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
