import React from "react";
import classNames from "classnames/bind";
import styles from "./Project.module.scss";
import Project from "./Project";
const cx = classNames.bind(styles);

export default function ProjectMain(props) {
  const { dataProjectFromHome } = props;

  return (
    <div className={cx("wrapper")}>
      <div className={cx("top")}>
        <div className={cx("title")}>
          <h1>Our Ongoing Projects</h1>
          <span></span>
        </div>
        <div className={cx("bottom-title")}>
          <button>Recent Projects</button>
          <p>Help Stop Illegal Widlife Trafficking</p>
        </div>
      </div>
      <div className={cx('flex')}>
          {dataProjectFromHome.map((project,idx) => {
            return (
              <Project
                key={idx}
                imgProject={project.img}
                titleProject={project.title}
                summaryProject={project.summary}
                challengeProject={project.challenge}
                solutionProject={project.solution}
                impactProject={project.impact}
                descProject={project.desc}
                timeProject={project.time}
              ></Project>
            );
          })}
      </div>
    </div>
  );
}
