import React from "react";
import classNames from "classnames/bind";
import styles from "./Footer.module.scss";
import FacebookIcon from "@mui/icons-material/Facebook";
import TwitterIcon from "@mui/icons-material/Twitter";
import YouTubeIcon from "@mui/icons-material/YouTube";
import InstagramIcon from "@mui/icons-material/Instagram";
import { Link } from "react-router-dom";
const cx = classNames.bind(styles);

export default function Footer() {
  return (
    <div>
      <div className={cx("wrapper")}>
        <div className={cx("top-foot")}>
          <div className={cx("line-1")}>
            <ul>
              <li>About Us</li>
              <Link to="/who-we-are">
                <li className={cx("li")}>Who We Are</li>
              </Link>
              <Link to="/team">
                <li className={cx("li")}>Team</li>
              </Link>
              <Link to="partners">
                <li className={cx("li")}>Partners</li>
              </Link>
            </ul>
          </div>
         
          <div className={cx("line-1")}>
            <ul>
              <li>Issues We Work On</li>
              <Link to="/animal-rescue">
                <li className={cx("li")}>Animal Rescue</li>
              </Link>
              <Link to="/animal-shelters">
                <li className={cx("li")}>Animal Shelters</li>
              </Link>
              <Link to="/spay">
                <li className={cx("li")}>Spay And Neuter</li>
              </Link>
              <Link to="/care">
                <li className={cx("li")}>Veterinary Care</li>
              </Link>
            </ul>
          </div>
          <div className={cx("line-1")}>
            <ul>
              
                <li>Contact Us</li>
                <Link to='/contact-us'>
                  <li className={cx("li")}>Contact Us</li>
                  <li className={cx("li")}>FAQ</li>
                  <li className={cx("li")}>Make a complaint</li>
                </Link>
            
            </ul>
          </div>
        </div>
        <div className={cx("bottom-foot")}>
          <div className={cx("left")}>
            <p>Copyright  2022 Hope Donors. All rights reserved.</p>
            <p>| Privacy Policy | Terms of use</p>
          </div>
          <div className={cx("right")}>
            <FacebookIcon
              style={{
                color: "#a8b0a6",
                marginLeft: "10px",
                marginRight: "10px",
              }}
            ></FacebookIcon>
            <TwitterIcon
              style={{
                color: "#a8b0a6",
                marginLeft: "10px",
                marginRight: "10px",
              }}
            ></TwitterIcon>
            <YouTubeIcon
              style={{
                color: "#a8b0a6",
                marginLeft: "10px",
                marginRight: "10px",
              }}
            ></YouTubeIcon>
            <InstagramIcon
              style={{
                color: "#a8b0a6",
                marginLeft: "10px",
                marginRight: "10px",
              }}
            ></InstagramIcon>
          </div>
        </div>
      </div>
    </div>
  );
}
