import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import React, { useContext, useState } from "react";
import { CartContext } from "../../Context/CartContext";
import classNames from "classnames/bind";
import styles from "./DetailProject.module.scss";
import "./DetailProjectd.css";
import RightSide from "../AnimalRescue/RightSide";

export default function DetailProject(props) {
  const cx = classNames.bind(styles);
  const { animalRescueData } = props;
  const { detailProject } = useContext(CartContext);
  const [typeInput, setTypeInput] = useState("");
  const [typeInput2, setTypeInput2] = useState("");

  const [typeInput3, setTypeInput3] = useState("");

  const [typeInput4, setTypeInput4] = useState("");
  const [red, setRed] = useState(false);
  const [red2, setRed2] = useState(false);

  const [red3, setRed3] = useState(false);

  const [red4, setRed4] = useState(false);
  const handleChange = (e) => {
    setTypeInput(e.target.value);
  };
  const handleChange2 = (e) => {
    setTypeInput2(e.target.value);
  };
  const handleChange3 = (e) => {
    setTypeInput3(e.target.value);
  };
  const handleChange4 = (e) => {
    setTypeInput4(e.target.value);
  };
  const handleReset = () => {
    if (typeInput.length < 5) {
      setRed(true);
      setRed2(true);
      toast.warn("Please enter more than 5 characters");

      setTypeInput("");
    } else {
      setTypeInput(typeInput);
    }
    if (typeInput2.length < 1) {
      setRed2(true);
      toast.warn("Please enter your name");

      setTypeInput2("");
    } else {
      setTypeInput2(typeInput2);
    }
    if (typeInput3.length < 2) {
      setRed3(true);
      toast.warn("Please enter email with correct syntax");

      setTypeInput3("");
    } else {
      setTypeInput3(typeInput3);
    }
    if (typeInput4.length < 5) {
      setRed4(true);
      setTypeInput4("");
      toast.warn("Please enter the website name");
    } else {
      setTypeInput4(typeInput4);
    }
    if (
      typeInput.length > 5 &&
      typeInput2.length > 1 &&
      typeInput3.length > 5 &&
      typeInput4.length > 5
    ) {
      toast.success(
        "Submitted information is complete. Thanks for the comment"
      );
      setTypeInput("");
      setTypeInput2("");
      setTypeInput3("");
      setTypeInput4("");
      setRed(false);
      setRed2(false);

      setRed3(false);

      setRed4(false);
    } else {
    }
  };

  return (
    <div style={{ background: "white" }}>
      {detailProject.map((detail) => {
        return (
          <div>
            <div className={cx("wrapper")}>
              <div className={cx("wrap")}>
                <div className={cx("left-side")}>
                  <div className={cx("card")}>
                    <div className={cx("title-card")}>
                      <p>Leave a Comment / Animal Rescue / By James Brown</p>
                      <h2>{detail.addTitleAnimal}</h2>
                    </div>
                    <div className={cx("img")}>
                      <img src={detail.addImgAnimal} alt=""></img>
                    </div>
                    <div className={cx("desc")}>
                      <div className={cx("summary")}>
                        <h2>Summary</h2>
                        <p>{detail.addSummaryAnimal}</p>
                      </div>
                      <div className={cx("challenge")}>
                        <h2>Challenge</h2>
                        <p>{detail.addChallengeAnimal}</p>
                      </div>
                      <div className={cx("solution")}>
                        <h2>Solution</h2>
                        <p>{detail.addSolutionAnimal}</p>
                      </div>
                      <div className={cx("impact")}>
                        <h2>Long-Term Impact</h2>
                        <p>{detail.addImpactAnimal}</p>
                      </div>
                      <button className={cx("button")}>
                        <div style={{ display: "flex", alignItems: "center" }}>
                          Donate Now
                        </div>
                      </button>
                    </div>
                    <div className={cx("valid-form")}>
                      <div className={cx("title-form")}>
                        <h2>Leave A Comment</h2>
                        <p>
                          Your email address will not be published. Required
                          fields are marked *
                        </p>
                      </div>
                      <div className={cx("top-form")}>
                        <textarea
                          placeholder="Type here ..."
                          className={cx(`form-1 ${red ? "redActive" : ""}`)}
                          onChange={handleChange}
                          value={typeInput}
                        ></textarea>
                        <ToastContainer autoClose={2000} pauseOnHover={false} />
                      </div>
                      <div className={cx("bottom-form")}>
                        <input
                          value={typeInput2}
                          onChange={handleChange2}
                          className={cx(`form-2 ${red2 ? "redActive" : ""}`)}
                          placeholder="Name"
                        ></input>
                        <input
                          value={typeInput3}
                          onChange={handleChange3}
                          className={cx(`form-3 ${red3 ? "redActive" : ""}`)}
                          placeholder="Email"
                        ></input>
                        <input
                          value={typeInput4}
                          onChange={handleChange4}
                          className={cx(`form-4 ${red4 ? "redActive" : ""}`)}
                          placeholder="Website"
                        ></input>
                      </div>
                      {/* <button onClick={handleReset}>REset</button> */}
                    </div>
                    {/* <Link to='/donate'> */}
                    <button className={cx("button")} onClick={handleReset}>
                      <div style={{ display: "flex", alignItems: "center" }}>
                        Donate Now
                      </div>
                    </button>
                    {/* </Link> */}
                  </div>
                </div>
              </div>
              <div className={cx("right-side")}>
                <div className={cx("search-input")}>
                  <p>Search</p>
                  <div className={cx("input")}>
                    <input type="text" placeholder="Search"></input>
                    <button>Search</button>
                  </div>
                </div>
                {/* <div className={cx("recent")}>
            <h2>Recent Posts</h2>
            {animalRescueData.map((title) => {
              return (
                <Link to="/dog-detail">
                  <p onClick={handleClick}>{title.title}</p>
                </Link>
              );
            })}
          
          </div> */}
                <h2>Recent Posts</h2>
                {animalRescueData.map((animal) => {
                  return (
                    <RightSide
                      imgAnimal={animal.img}
                      titleAnimal={animal.title}
                      summaryAnimal={animal.summary}
                      challengeAnimal={animal.challenge}
                      solutionAnimal={animal.solution}
                      impactAnimal={animal.impact}
                    ></RightSide>
                  );
                })}
              </div>
            </div>
          </div>
        );
      })}
    </div>
  );
}
