import React, { useContext } from "react";
import classNames from "classnames/bind";
import styles from "../AnimalRescue/AnimalRescueCard.module.scss";
import { Link } from "react-router-dom";
import { CartContext } from "../../Context/CartContext";
const cx = classNames.bind(styles);
export default function RightSide(props) {
  const { setDetailProject } = useContext(CartContext);
  const {
    imgAnimal,
    titleAnimal,
    summaryAnimal,
    challengeAnimal,
    solutionAnimal,
    impactAnimal,
  } = props;
  const handleClick = () => {
    const newData = {
      addImgAnimal: imgAnimal,
      addTitleAnimal: titleAnimal,
      addSummaryAnimal: summaryAnimal,
      addChallengeAnimal: challengeAnimal,
      addSolutionAnimal: solutionAnimal,
      addImpactAnimal: impactAnimal,
    };
    setDetailProject([newData]);
  };
  return (
    <div>
      <div className={cx("recent")}>

        <Link to="/detail-project">
          <p onClick={handleClick}>{titleAnimal}</p>
        </Link>
      </div>
    </div>
  );
}
