import React from "react";
import classNames from "classnames/bind";
import styles from "./Content.module.scss";
const cx = classNames.bind(styles);
export default function Content() {
  return (
    <div style={{background:'white'}}>
      <div className={cx("wrapper")}>
        <div className={cx("title")}>
          <h2>
            When You Give To Hopedonors, You Become More Than Just A Donor, You
            Become Our Partner
          </h2>
        </div>
        <div className={cx("desc")}>
          <p>
            Our partners come in all shape and sizes and include individual
            volunteers, small businesses that want to make a difference and
            large corporations that like to be active in charity works.{" "}
          </p>
          <p>
            Working with our partners is essential to achieve our goals, and
            that is why we actively seeking to develop partners. These
            partnerships enable us to be more active where is needed and helps
            us to achieve more, while we hope that our partners also benefit
            from their connection with Hope Donors. We believe our partners and
            connections help us continue the growth of our work and we greatly
            value our partners and connections.
          </p>
        </div>
        <div className={cx("title2")}>
          <h2>Become A Partner</h2>
        </div>
        <div className={cx("desc2")}>
          <p>
            We welcome partners that like to make a difference and Together, we
            can reduce needless cruelty to animals.
          </p>
        </div>
        <div className={cx("btn")}>
          <button>CONTACT US</button>
          <button>BECOME A PARTNER</button>
        </div>
      </div>
    </div>
  );
}
