import React, { useState } from "react";
import classNames from "classnames/bind";
import styles from "./ContactUs.module.scss";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
const cx = classNames.bind(styles);
export default function ContactUs() {
  const [name, setName] = useState("");
  const nameChange = (e) => {
    setName(e.target.value);
  };
  const [email, setEmail] = useState("");
  const emailChange = (e) => {
    setEmail(e.target.value);
  };
  const [message, setMessage] = useState("");
  const messageChange = (e) => {
    setMessage(e.target.value);
  };
  const handleClick = () => {
    if (name.length < 2) {
      toast.warn("Please enter your name");
      setName("");
    } else {
      setName(name);
    }
    if (email.length < 2) {
      toast.warn("Please enter your email");
      setEmail("");
    } else {
      setEmail(email);
    }
    if (message.length < 2) {
      toast.warn("Please enter your message");
      setMessage("");
    } else {
      setMessage(message);
    }
    if (name.length > 2 && email.length > 2 && message.length > 2) {
      toast.success("Submitted information is complete.");
    }
  };
  return (
    <div style={{ background: "#f3f2ed" }}>
      <ToastContainer></ToastContainer>
      <div className={cx("wrapper")}>
        <div className={cx("banner")}>
          <img
            width="100vw"
            height="60vh"
            style={{ width: "100vw", height: "80vh" }}
            src="https://hopedonors.org/wp-content/uploads/2021/09/Contact-us.webp"
            alt=""
          ></img>
        </div>
        <div className={cx("content")}>
          <h2>Get In Touch With Us. We'd Love To Hear From You.</h2>
          <div className={cx("valid-form")}>
            <div className={cx("left-form")}>
              <h3>Hope Donors</h3>
              <div className={cx("email")}>
                <h4>Email:</h4>
                <p> info@hopedonors.org</p>
              </div>
              <div className={cx("address")}>
                <h4>Address:</h4>
                <p>920 9 Ave SW Calgary, Alberta t2p 2t9</p>
              </div>
              <div className={cx("hours")}>
                <h4> Hours:</h4>
                <p>
                  Monday, Tuesday, Wednesday, Thursday, Friday, Saturday, Sunday
                  09:00  17:00
                </p>
              </div>
              <div className={cx("phone")}>
                <h4>Customer Service:</h4>
                <p> +1-304-454-3332</p>
              </div>
            </div>
            <div className={cx("right-form")}>
              <p>Name</p>
              <input
                onChange={nameChange}
                value={name}
                className={cx("nameInput")}
                placeholder="Enter your name"
              ></input>
              <p>Email</p>
              <input
                value={email}
                onChange={emailChange}
                className={cx("emailInput")}
                placeholder="Enter your Email address"
              ></input>
              <p>Message</p>
              <textarea
                value={message}
                onChange={messageChange}
                className={cx("messageInput")}
                placeholder="Message"
              ></textarea>
              <button onClick={handleClick}>Send</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
