import React, { useContext, useState } from "react";
import classNames from "classnames/bind";
import styles from "./CartPage.module.scss";
import { CartContext } from "../../Context/CartContext";
import { Link } from "react-router-dom";
const cx = classNames.bind(styles);

export default function CartPage(props) {

  const { myCart, setDetail } = useContext(CartContext);
  // console.log(myCart)
  const {
    idDog,
    descDog,
    lifeDog,
    weightDog,
    heightDog,
    breedDog,
    imgDog,
    breedGroupDog,
  } = props;
  const detailClick = () => {
    const newItems2 = {
      addIdDog: idDog,
      addBreedDog: breedDog,
      addImg: imgDog,
      addDescDog: descDog,
      addHeightDog: heightDog,
      addWeightDog: weightDog,
      addLifeDog: lifeDog,
      addBreedGroupDog: breedGroupDog,
    };
    //Thm ch cha ( ch ch l setDetail ) bng vi danh sch va to. Nh l bin thm [ ]  th hin mt mng
    setDetail([newItems2]);
  };
  return (
    <>
      <Link to="/dogdetail" onClick={detailClick}>
        <div className={cx("all-cart")}>
          <div key={idDog} className={cx("carts")}>
            <div className={cx("cart-img")}>
              <img className={cx("img")} src={imgDog} alt=""></img>
            </div>
            <div className={cx("cart-name")}>
              <h1 style={{ textTransform: "capitalize" }}>{breedDog}</h1>
            </div>
            <div className={cx("cart-price")}>
              <h1 style={{ textTransform: "capitalize" }}>{breedGroupDog}</h1>
            </div>
            {/* <div className={cx("un-select")}>
                      <button onClick={handleClick}>X</button>
                    </div> */}
          </div>
        </div>
      </Link>
      
      {/* <div className={cx("total")}>
          <h1>{total}$</h1>
        </div> */}
    </>
  );
}
