import React, { useContext } from "react";
import classNames from "classnames/bind";
import styles from "./AnimalRescue.module.scss";
import AnimalRescueCard from "./AnimalRescueCard";
import { Link } from "react-router-dom";
import { CartContext } from "../../Context/CartContext";
import RightSide from "./RightSide";
const cx = classNames.bind(styles);
export default function AnimalRescue(props) {
  const { animalRescueData } = props;
  return (
    <div style={{ background: "white" }} className={cx("wrapper")}>
      <div className={cx("all")}>
        <div className={cx("card")}>
          <div className={cx("title")}>
            <h1>Animal Rescued</h1>
          </div>
          {animalRescueData.map((animal) => {
            return (
              <AnimalRescueCard
                imgAnimal={animal.img}
                titleAnimal={animal.title}
                summaryAnimal={animal.summary}
                challengeAnimal={animal.challenge}
                solutionAnimal={animal.solution}
                impactAnimal={animal.impact}
              ></AnimalRescueCard>
            );
          })}
        </div>
        <div className={cx("right-side")}>
          <div className={cx("search-input")}>
            <p>Search</p>
            <div className={cx("input")}>
              <input type="text" placeholder="Search"></input>
              <button>Search</button>
            </div>
          </div>
          {/* <div className={cx("recent")}>
            <h2>Recent Posts</h2>
            {animalRescueData.map((title) => {
              return (
                <Link to="/dog-detail">
                  <p onClick={handleClick}>{title.title}</p>
                </Link>
              );
            })}
          
          </div> */}
          <h2>Recent Posts</h2>
          {animalRescueData.map((animal) => {
            return (
              <RightSide
                imgAnimal={animal.img}
                titleAnimal={animal.title}
                summaryAnimal={animal.summary}
                challengeAnimal={animal.challenge}
                solutionAnimal={animal.solution}
                impactAnimal={animal.impact}
              ></RightSide>
            );
          })}
        </div>
      </div>
    </div>
  );
}
