import React from "react";
import DogCard from "./DogCard";
import classNames from "classnames/bind";
import styles from "./DogsPage.module.scss";
const cx = classNames.bind(styles);

export default function DogsPage(props) {
  //Nhn props allDogs API (json file)
  const { allDogs } = props;
  return (
    <div className={cx("wrapper")}>
      <div>
        <img
          width="100vw"
          height="60vh"
          style={{ width: "100vw", height: "80vh" }}
          src="https://cdn.shopify.com/s/files/1/0113/3520/4922/collections/brutus-broth-product-collection_2x_1080x_f094be5d-8e31-4a97-b493-9e69b4cecb91_1728x.jpg?v=1660168082"
          alt=""
        ></img>
        <h2>Welcome To Dog Breed Information</h2>
      </div>

      {/* map ci API json */}
      <div className={cx('padding')}>
        {allDogs.map((dog) => (
          <div className={cx("card")} key={dog.id}>
            {/* idDog l tn, dog.id l th cn truyn ra ngoi cho DogCard nhn,...... */}
            <DogCard
              idDog={dog.id}
              breedDog={dog.breed}
              descDog={dog.description}
              imgDog={dog.image}
              heightDog={dog.height}
              weightDog={dog.weight}
              lifeDog={dog.life}
              breedGroupDog={dog.breedGroup}
            ></DogCard>
          </div>
        ))}
      </div>
    </div>
  );
}
