import React, { useContext } from "react";
import classNames from "classnames/bind";
import styles from "./AnimalRescueCard.module.scss";
import { CartContext } from "../../Context/CartContext";
import { Link } from "react-router-dom";
const cx = classNames.bind(styles);

export default function AnimalRescueCard(props) {
  const { setDetailProject } = useContext(CartContext);
  const {
    imgAnimal,
    titleAnimal,
    summaryAnimal,
    challengeAnimal,
    solutionAnimal,
    impactAnimal,
  } = props;
  const handleClick = () => {
    const newData = {
      addImgAnimal: imgAnimal,
      addTitleAnimal: titleAnimal,
      addSummaryAnimal: summaryAnimal,
      addChallengeAnimal: challengeAnimal,
      addSolutionAnimal: solutionAnimal,
      addImpactAnimal: impactAnimal,
    };
    setDetailProject([newData]);
  };
  return (
    <div >
      <div  className={cx("wrapper")}>
        <div className={cx("wrap")}>
          <Link onClick={handleClick} to="/detail-project">
            <div className={cx("left-side")}>
              <div className={cx("card")}>
                <div className={cx("img")}>
                  <img src={imgAnimal} alt=""></img>
                </div>
                <div className={cx("title-card")}>
                  <h2>{titleAnimal}</h2>
                  <p>Leave a Comment / Animal Rescue / By James Brown</p>
                </div>
                <div className={cx("desc-card")}>
                  <p>{summaryAnimal}</p>
                </div>
                <div className={cx("read-more")}>
                  <p>Read More </p>
                </div>
              </div>
            </div>
          </Link>
        </div>

      </div>
    </div>
  );
}
