import React, { useState } from "react";
import { Link } from "react-router-dom";
import classNames from "classnames/bind";
import styles from "./Navbar.module.scss";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import { Badge } from "@mui/material";
import useScrollY from "../Hooks/ScrollY";
import ArrowDropDownIcon from "@mui/icons-material/ArrowDropDown";
import DehazeIcon from "@mui/icons-material/Dehaze";
import "./Navbar.css";
const cx = classNames.bind(styles);

export default function Navbar() {
  const [responsive, setResponsive] = useState(false);
  const handleToggle = () => {
    setResponsive((responsive) => !responsive);
  };
  const hideBtn = () => {
    setResponsive((responsive) => !responsive);
  };
  const [scrollY] = useScrollY();
  return (
    <div>
      <nav
        //stickybar
        style={
          scrollY < 50
            ? { backgroundColor: "whitesmoke", transition: "all 1.5s ease" }
            : {
                backgroundColor: "rgba(245, 245, 245, 0.55)",
                transition: "all 1.5s ease",
                backdropFilter: "blur(10px)",
              }
        }
        //--------------------------------
        className={cx("navbar")}
      >
        <div className={cx("left-nav")}>
          <div className={cx("flex2")}>
            <Link to="/">
              <div className={cx("left-nav")}>
                <div className={cx("logo")}>
                  <img
                    src="https://hopedonors.org/wp-content/uploads/2022/02/hopedonors-logo-2.png"
                    alt=""
                  ></img>
                </div>
                <div className={cx("logo-name")}>
                  <p>Hope Donors</p>
                  <p>Save The Loving Paws</p>
                </div>
              </div>
            </Link>
            <div className={cx("bars")} onClick={handleToggle}>
              <DehazeIcon></DehazeIcon>
            </div>
          </div>
          <div className={cx(`mid-nav ${responsive ? "active" : ""}`)}>
            <div className={cx(`flex`)}>
              <div className={cx("dropdown")}>
                <button className={cx("dropbtn")}>
                  About Us <ArrowDropDownIcon></ArrowDropDownIcon>
                </button>
                <div onClick={hideBtn} className={cx("dropdown-content")}>
                  <Link to="/who-we-are" className={cx("link")}>
                    Who we are
                  </Link>
                  <Link to="/team" className={cx("link")}>
                    Team
                  </Link>
                  <Link to="/partners" className={cx("link")}>
                    Partners
                  </Link>
                </div>
              </div>

              <div className={cx("dropdown")}>
                <button className={cx("dropbtn")}>
                  Issues We Work On
                  <ArrowDropDownIcon></ArrowDropDownIcon>
                </button>
                <div onClick={hideBtn} className  ={cx("dropdown-content")}>
                  <Link to="/animal-rescue" className={cx("link")}>
                    Animal Rescue
                  </Link>
                  <Link to="/animal-shelters" className={cx("link")}>
                    Animal Shelters
                  </Link>
                  <Link to="/spay" className={cx("link")}>
                    Spay And Neuter
                  </Link>
                  <Link to="/care" className={cx("link")}>
                    Veterinary Care
                  </Link>
                </div>
              </div>
              <Link onClick={hideBtn} to="/contact-us" className={cx("news")}>
                Contact Us
              </Link>

              <Link onClick={hideBtn} className={cx("buy-dog")} to="/dogs">
                Dog Breeds
              </Link>
              {/* <div className={cx("cart")}> */}
              <Link onClick={hideBtn} className={cx("news")} to="/cart">
                {/* <Badge badgeContent={4} color="primary">
              <ShoppingCartIcon />
            </Badge> */}
                Your Favourite Breeds
              </Link>
              {/* </div> */}
            </div>
          </div>
        </div>
      </nav>
    </div>
  );
}
